<template>


  <Form :model="formItem" :label-width="80">
    <FormItem label="用户名：">
      <Input v-model="formItem.input" placeholder="请输入你的用户名："></Input>
    </FormItem>
    <FormItem label="密码：">
      <Input v-model="formItem.input" placeholder="请输入密码："></Input>

      <Button to="/Home" type="primary" style="float: right;margin-top: 20px;">登录</Button>
      <Button type="link" style="float: right;margin-top: 20px;" :class="activeclass">忘记密码</Button>
    </FormItem>

  </Form>

</template>

<script>
    export default {
        name: "login",
      data () {
        return {
          formItem: {
            input: '',
          }
        }
      }
    }
</script>

